import { NgClass } from '@angular/common';
import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { RouterModule } from '@angular/router';
import { Company } from '@biz-nest/commerce-client';
import { FrontendLogger, IfilterProps } from '@biz-nest/core-client';
import { openClose, TooltipComponent } from '@front-shared-lib';

@Component({
  selector: 'app-company-cards',
  standalone: true,
  imports: [
    RouterModule,
    NgClass,
    TooltipComponent
  ],
  animations: [
    openClose
  ],
  templateUrl: './company-cards.component.html',
  styleUrl: './company-cards.component.scss'
})
export class CompanyCardsComponent
implements OnInit {
  @Output() filterBy = new EventEmitter<IfilterProps>();
  companysTotal = 0;
  activCompanysTotal = 0;
  inActivCompanysTotal = 0;
  recentlyAdded = 0;
  totalCompanysGrowth = 0;
  activeCompanysGrowth = 0;
  inActiveCompanysGrowth = 0;
  thisMonthDate = new Date(new Date().getFullYear(), new Date().getMonth(), 0);

  constructor() {}

  ngOnInit(): void {
    this.getAllCompaniesTotal();
    this.getActiveCompaniesTotal();
    this.getInActiveCompaniesTotal();
    this.getLatestCompaniesTotal();
    this.getTotalCompanyGrowthDiff();
  }

  async getAllCompaniesTotal() {
    try {
      const { count } = await Company.filterAll({
        returnEmptyArray: true
      });

      this.companysTotal = count;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getActiveCompaniesTotal() {
    try {
      const { count } = await Company.filterAll({
        propFilter: {
          xibi: true
        },
        returnEmptyArray: true
      });

      this.activCompanysTotal = count;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getInActiveCompaniesTotal() {
    try {
      const { count } = await Company.filterAll({
        propFilter: {
          blocked: true
        },
        returnEmptyArray: true
      });

      this.inActivCompanysTotal = count;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getLatestCompaniesTotal() {
    try {
      const { count } = await Company.filterAll({
        propFilter: {
          blocked: false
        },
        propSort: {
          createdAt: 'desc'
        },
        comparisonFilter: [
          {
            field: 'createdAt',
            fieldValue: this.thisMonthDate,
            operator: 'gte'
          }
        ],
        returnEmptyArray: true
      });

      this.recentlyAdded = count;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getTotalCompanyGrowthDiff() {
    try {
      const lastMonth = await Company.filterAll({
        comparisonFilter: [
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth() - 1, 1),
            operator: 'gte'
          },
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth(), 0),
            operator: 'lte'
          }
        ]
      });

      const thisMonth = await Company.filterAll({
        comparisonFilter: [
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth(), 1),
            operator: 'gte'
          },
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth() + 1, 0),
            operator: 'lte'
          }
        ]
      });

      this.totalCompanysGrowth = ((thisMonth.count - lastMonth.count) / (lastMonth.count || 1) * 100) || 0;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getActiveCompanyGrowthDiff() {
    try {
      const lastMonth = await Company.filterAll({
        propFilter: {
          blocked: false
        },
        comparisonFilter: [
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth() - 1, 1),
            operator: 'gte'
          },
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth(), 0),
            operator: 'lte'
          }
        ]
      });

      const thisMonth = await Company.filterAll({
        propFilter: {
          blocked: false
        },
        comparisonFilter: [
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth(), 1),
            operator: 'gte'
          },
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth() + 1, 0),
            operator: 'lte'
          }
        ]
      });

      this.activeCompanysGrowth = ((thisMonth.count - lastMonth.count) / (lastMonth.count || 1) * 100) || 0;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }


  async getInActiveCompanyGrowthDiff() {
    try {
      const lastMonth = await Company.filterAll({
        propFilter: {
          blocked: true
        },
        comparisonFilter: [
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth() - 1, 1),
            operator: 'gte'
          },
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth(), 0),
            operator: 'lte'
          }
        ]
      });

      const thisMonth = await Company.filterAll({
        propFilter: {
          blocked: true
        },
        comparisonFilter: [
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth(), 1),
            operator: 'gte'
          },
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth() + 1, 0),
            operator: 'lte'
          }
        ]
      });

      this.inActiveCompanysGrowth = ((thisMonth.count - lastMonth.count) / (lastMonth.count || 1) * 100) || 0;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }
}

